        <view class="feature-content">
          <text class="feature-title">{{ feature.title }}</text>
          <text class="feature-desc">{{ feature.description }}</text>
        </view>
      </view>
    </view>
    
    <!-- ÁôªÂΩïÊèêÁ§∫ -->
    <view class="login-prompt">
      <text class="prompt-text">‰ΩøÁî®ÂæÆ‰ø°ÁôªÂΩïÔºå‰∫´ÂèóÂÆåÊï¥Â≠¶‰π†ÂäüËÉΩ</text>
    </view>
    
    <!-- ÁôªÂΩïÊåâÈíÆÂå∫Âüü -->
    <view class="login-actions">
      <!-- ÂæÆ‰ø°ÁôªÂΩïÊåâÈíÆ -->
      <button 
        class="wechat-login-btn"
        :class="{ 'loading': authStore.loading.login }"
        :disabled="authStore.loading.login || showLoadingMask"
        @click="handleWechatLogin"
        open-type="getUserInfo"
        @getuserinfo="onGetUserInfo"
      >
        <uni-icons 
          v-if="!authStore.loading.login && !showLoadingMask" 
          type="weixin" 
          size="20" 
          color="#fff" 
          style="margin-right: 10rpx;" 
        />
        <uni-icons 
          v-else
          type="spinner-cycle" 
          size="20" 
          color="#fff" 
          style="margin-right: 10rpx;" 
        />
        <text class="btn-text">
          {{ getLoginButtonText() }}
        </text>
      </button>
      
      <!-- Ê∏∏ÂÆ¢ÊµèËßàÊåâÈíÆ -->
      <button 
        class="guest-browse-btn"
        :disabled="authStore.loading.login"
        @click="handleGuestBrowse"
      >
        <text class="btn-text">ÊöÇ‰∏çÁôªÂΩïÔºåÊ∏∏ÂÆ¢ÊµèËßà</text>
      </button>
    </view>
    
    <!-- ÁôªÂΩïËØ¥Êòé -->
    <view class="login-notice">
      <text class="notice-text">
        ÁôªÂΩïÂêéÂèØ‰ΩøÁî®Êñá‰ª∂‰∏ä‰º†„ÄÅÂ≠¶‰π†ËøõÂ∫¶ÂêåÊ≠•„ÄÅ‰∏™‰∫∫ËµÑÊñôÁÆ°ÁêÜÁ≠âÂÆåÊï¥ÂäüËÉΩ
      </text>
    </view>
    
    <!-- Áî®Êà∑ÂçèËÆÆ -->
    <view class="agreement-section">
      <text class="agreement-text">
        ÁôªÂΩïÂç≥Ë°®Á§∫ÂêåÊÑè
        <text class="link-text" @click="showUserAgreement">„ÄäÁî®Êà∑ÂçèËÆÆ„Äã</text>
        Âíå
        <text class="link-text" @click="showPrivacyPolicy">„ÄäÈöêÁßÅÊîøÁ≠ñ„Äã</text>
      </text>
    </view>
    
    <!-- ÁâàÊú¨‰ø°ÊÅØ -->
    <view class="version-info">
      <text class="version-text">ÁâàÊú¨ {{ appVersion }}</text>
    </view>
    
    <!-- Âä†ËΩΩÈÅÆÁΩ© -->
    <view v-if="showLoadingMask" class="loading-mask">
      <view class="loading-content">
        <uni-icons type="spinner-cycle" size="40" color="#007AFF" />
        <text class="loading-text">{{ loadingText }}</text>
      </view>
    </view>
  </view>
</template>

<script setup>
import { ref, reactive, computed, onMounted } from 'vue'
import { useAuthStore } from '@/stores/auth.js'
import { RouteGuard } from '@/utils/auth.js'
import NavigationGuard from '@/utils/navigationGuard.js'

// Áä∂ÊÄÅÁÆ°ÁêÜ
const authStore = useAuthStore()

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const appVersion = ref('1.0.0')
const showLoadingMask = ref(false)
const loadingText = ref('ÁôªÂΩï‰∏≠...')

// ÂäüËÉΩÁâπËâ≤‰ªãÁªç
const features = reactive([
  {
    title: 'Êô∫ËÉΩÂ≠¶‰π†',
    description: '‰∏™ÊÄßÂåñÂ≠¶‰π†Ë∑ØÂæÑÊé®Ëçê',
    icon: 'gear',
    color: '#007AFF'
  },
  {
    title: 'È¢òÂ∫ìÁªÉ‰π†',
    description: 'Êµ∑ÈáèÈ¢òÁõÆÔºåÁ≤æÂáÜÁªÉ‰π†',
    icon: 'compose',
    color: '#28a745'
  },
  {
    title: 'ËøõÂ∫¶ÂêåÊ≠•',
    description: 'Â§öÁ´ØÊï∞ÊçÆÂÆûÊó∂ÂêåÊ≠•',
    icon: 'cloud-upload',
    color: '#ffc107'
  },
  {
    title: 'Êñá‰ª∂‰∏ä‰º†',
    description: 'ÊîØÊåÅÊñáÊ°£ÊâπÈáèËß£Êûê',
    icon: 'folder',
    color: '#17a2b8'
  }
])

// ËÆ°ÁÆóÂ±ûÊÄß
const canLogin = computed(() => {
  return !authStore.loading.login && !showLoadingMask.value
})

// Ëé∑ÂèñÁôªÂΩïÊåâÈíÆÊñáÊú¨
const getLoginButtonText = () => {
  if (showLoadingMask.value) {
    return loadingText.value || 'Â§ÑÁêÜ‰∏≠...'
  }
  if (authStore.loading.login) {
    return 'ÁôªÂΩï‰∏≠...'
  }
  return 'ÂæÆ‰ø°‰∏ÄÈîÆÁôªÂΩï'
}

// LogoÂä†ËΩΩÂ§±Ë¥•Â§ÑÁêÜ
const onLogoError = () => {
  console.warn('LogoÂä†ËΩΩÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§ÂõæÊ†á')
}

// ÂæÆ‰ø°ÁôªÂΩïÂ§ÑÁêÜ
const handleWechatLogin = async () => {
  console.log('üîò Áî®Êà∑ÁÇπÂáªÂæÆ‰ø°ÁôªÂΩïÊåâÈíÆ')
  
  // Èò≤Ê≠¢ÈáçÂ§çÁÇπÂáª
  if (!canLogin.value) {
    console.warn('‚ö†Ô∏è ÂΩìÂâçÊ≠£Âú®ÁôªÂΩï‰∏≠ÔºåÂøΩÁï•ÈáçÂ§çÁÇπÂáª')
    return
  }
  
  try {
    showLoadingMask.value = true
    loadingText.value = 'Ê≠£Âú®Ëé∑ÂèñÂæÆ‰ø°ÊéàÊùÉ...'
    
    console.log('üöÄ ÂºÄÂßãÂæÆ‰ø°ÁôªÂΩïÊµÅÁ®ã')
    
    // Â∞ùËØïÂ∏¶Áî®Êà∑‰ø°ÊÅØÁöÑÂÆåÊï¥ÁôªÂΩï
    await authStore.loginWithWechat(true, false)
    
    loadingText.value = 'ÁôªÂΩïÊàêÂäüÔºåË∑≥ËΩ¨‰∏≠...'
    console.log('üéâ ÂæÆ‰ø°ÁôªÂΩïÊàêÂäüÔºåÂáÜÂ§áË∑≥ËΩ¨')
    
    // ÁôªÂΩïÊàêÂäüÔºåË∑≥ËΩ¨Âà∞ÁõÆÊ†áÈ°µÈù¢
    await navigateAfterLogin()
    
  } catch (error) {
    console.error('‚ùå ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•:', error)
    
    // ÁâπÊÆäÂ§ÑÁêÜcodeÁõ∏ÂÖ≥ÈîôËØØ
    if (authStore.isCodeRelatedError && authStore.isCodeRelatedError(error)) {
      console.log('üîÑ Ê£ÄÊµãÂà∞codeÈîôËØØÔºåÊòæÁ§∫ÈáçËØïÈÄâÈ°π')
      uni.showModal({
        title: 'ÁôªÂΩïÂ§±Ë¥•',
        content: 'ÂæÆ‰ø°ÊéàÊùÉÁ†ÅÂ∑≤Â§±ÊïàÔºåÊòØÂê¶ÈáçÊñ∞Â∞ùËØïÁôªÂΩïÔºü',
        confirmText: 'ÈáçËØïÁôªÂΩï',
        cancelText: 'Á®çÂêéÂÜçËØï',
        success: async (res) => {
          if (res.confirm) {
            // Âª∂ËøüÂêéÈáçËØï
            setTimeout(() => {
              handleWechatLogin()
            }, 500)
          }
        }
      })
    } else {
      handleLoginError(error)
    }
  } finally {
    showLoadingMask.value = false
  }
}

// Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂõûË∞ÉÔºàÂÖºÂÆπÊóßÁâàÊú¨Ôºâ
const onGetUserInfo = async (event) => {
  console.log('Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ:', event.detail)
  
  if (event.detail.errMsg === 'getUserInfo:ok') {
    // Áî®Êà∑ÂêåÊÑèÊéàÊùÉÔºåÂ∑≤Âú® handleWechatLogin ‰∏≠Â§ÑÁêÜ
    return
  } else {
    // Áî®Êà∑ÊãíÁªùÊéàÊùÉÔºåÈúÄË¶ÅÈáçÊñ∞Ëé∑ÂèñcodeËøõË°åÂü∫Á°ÄÁôªÂΩï
    console.log('üîÑ Áî®Êà∑ÊãíÁªùÊéàÊùÉÔºåÂ∞ùËØïÂü∫Á°ÄÁôªÂΩïÔºà‰ΩøÁî®Êñ∞codeÔºâ')
    
    if (!canLogin.value) {
      console.warn('‚ö†Ô∏è ÂΩìÂâçÊ≠£Âú®ÁôªÂΩï‰∏≠ÔºåÂøΩÁï•ÈáçÂ§çËØ∑Ê±Ç')
      return
    }
    
    try {
      showLoadingMask.value = true
      loadingText.value = 'Ê≠£Âú®ËøõË°åÂü∫Á°ÄÁôªÂΩï...'
      
      // ‰ΩøÁî®Êñ∞ÁöÑcodeËøõË°åÂü∫Á°ÄÁôªÂΩïÔºàÊ†áËÆ∞‰∏∫ÈáçËØï‰ª•Âº∫Âà∂Ëé∑ÂèñÊñ∞codeÔºâ
      await authStore.loginWithWechat(false, true)
      
      loadingText.value = 'ÁôªÂΩïÊàêÂäüÔºåË∑≥ËΩ¨‰∏≠...'
      await navigateAfterLogin()
      
    } catch (error) {
      console.error('Âü∫Á°ÄÁôªÂΩïÂ§±Ë¥•:', error)
      handleLoginError(error)
    } finally {
      showLoadingMask.value = false
    }
  }
}

// Ê∏∏ÂÆ¢ÊµèËßàÂ§ÑÁêÜ
const handleGuestBrowse = () => {
  uni.showModal({
    title: 'Ê∏∏ÂÆ¢Ê®°Âºè',
    content: 'Ê∏∏ÂÆ¢Ê®°Âºè‰∏ãÈÉ®ÂàÜÂäüËÉΩÂèóÈôêÔºåÂª∫ËÆÆÁôªÂΩïÂêé‰ΩøÁî®ÂÆåÊï¥ÂäüËÉΩ„ÄÇÁ°ÆÂÆö‰ª•Ê∏∏ÂÆ¢Ë∫´‰ªΩÊµèËßàÂêóÔºü',
    confirmText: 'Á°ÆÂÆöÊµèËßà',
    cancelText: 'ËøîÂõûÁôªÂΩï',
    success: (res) => {
      if (res.confirm) {
        // Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ
        uni.reLaunch({
          url: '/pages/index/index'
        })
      }
    }
  })
}

// ÁôªÂΩïÂêéË∑≥ËΩ¨Â§ÑÁêÜ
const navigateAfterLogin = async () => {
  console.log('ÁôªÂΩïÊàêÂäüÔºåÂ§ÑÁêÜË∑≥ËΩ¨ÈÄªËæë')
  
  // Âª∂ËøüË∑≥ËΩ¨ÔºåËÆ©Áî®Êà∑ÁúãÂà∞ÊàêÂäüÁä∂ÊÄÅ
  setTimeout(() => {
    NavigationGuard.handleLoginSuccess()
  }, 1000)
}

// ÁôªÂΩïÈîôËØØÂ§ÑÁêÜ
const handleLoginError = (error) => {
  let title = 'ÁôªÂΩïÂ§±Ë¥•'
  let content = error.message || 'ÁôªÂΩïËøáÁ®ã‰∏≠ÂèëÁîüÊú™Áü•ÈîôËØØÔºåËØ∑Á®çÂêéÈáçËØï'
  
  // Ê†πÊçÆÈîôËØØÁ±ªÂûãÊòæÁ§∫‰∏çÂêåÊèêÁ§∫
  if (error.message && error.message.includes('ÂèñÊ∂à')) {
    title = 'ÁôªÂΩïÂèñÊ∂à'
    content = 'ÊÇ®ÂèñÊ∂à‰∫ÜÂæÆ‰ø°ÊéàÊùÉ„ÄÇÊÇ®ÂèØ‰ª•ÈÄâÊã©Ê∏∏ÂÆ¢ÊµèËßàÔºåÊàñÁ®çÂêéÈáçÊñ∞ÁôªÂΩïËé∑ÂèñÂÆåÊï¥ÂäüËÉΩ„ÄÇ'
  } else if (error.message && error.message.includes('ÁΩëÁªú')) {
    title = 'ÁΩëÁªúÈîôËØØ'
    content = 'ÁΩëÁªúËøûÊé•ÂºÇÂ∏∏ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËÆæÁΩÆÂêéÈáçËØï„ÄÇ'
  } else if (error.message && error.message.includes('ÊúçÂä°Âô®')) {
    title = 'ÊúçÂä°ÂºÇÂ∏∏'
    content = 'ÊúçÂä°Âô®ÊöÇÊó∂Êó†Ê≥ïÂìçÂ∫îÔºåËØ∑Á®çÂêéÈáçËØï„ÄÇ'
  }
  
  uni.showModal({
    title,
    content,
    showCancel: false,
    confirmText: 'Áü•ÈÅì‰∫Ü'
  })
}

// ÊòæÁ§∫Áî®Êà∑ÂçèËÆÆ
const showUserAgreement = () => {
  uni.showModal({
    title: 'Áî®Êà∑ÂçèËÆÆ',
    content: 'ËøôÈáåÂ∫îËØ•ÊòæÁ§∫ÂÆåÊï¥ÁöÑÁî®Êà∑ÂçèËÆÆÂÜÖÂÆπ...',
    showCancel: false,
    confirmText: 'Áü•ÈÅì‰∫Ü'
  })
}

// ÊòæÁ§∫ÈöêÁßÅÊîøÁ≠ñ
const showPrivacyPolicy = () => {
  uni.showModal({
    title: 'ÈöêÁßÅÊîøÁ≠ñ',
    content: 'ËøôÈáåÂ∫îËØ•ÊòæÁ§∫ÂÆåÊï¥ÁöÑÈöêÁßÅÊîøÁ≠ñÂÜÖÂÆπ...',
    showCancel: false,
    confirmText: 'Áü•ÈÅì‰∫Ü'
  })
}

// Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
const checkAuthState = () => {
  if (authStore.isAuthenticated) {
    console.log('Áî®Êà∑Â∑≤ÁôªÂΩïÔºåË∑≥ËΩ¨Ëá≥È¶ñÈ°µ')
    uni.reLaunch({
      url: '/pages/index/index'
    })
  }
}

onMounted(() => {
  // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªèÁôªÂΩï
  checkAuthState()
  
  // Ëé∑ÂèñÂ∫îÁî®ÁâàÊú¨‰ø°ÊÅØ
  // #ifdef APP-PLUS
  plus.runtime.getProperty(plus.runtime.appid, (widgetInfo) => {
    appVersion.value = widgetInfo.version
  })
  // #endif
})
</script>

